import axios from 'axios';
import qs from 'qs';

axios.defaults.timeout = 60000;
axios.defaults.baseURL = '';

//http request 拦截器
axios.interceptors.request.use(
  config => {
    // 表单形式提交
    if (config.dataType === 'json') {
      config.data = JSON.stringify(config.data);
      config.headers = {
        'Content-Type': 'application/json'
      }
    } else {
      config.data = qs.stringify(config.data);
      config.headers = {
        'Content-Type': 'application/x-www-form-urlencoded'
      }
    }
    return config;
  },
  error => {
    return Promise.reject(err);
  }
);

//http response 拦截器
axios.interceptors.response.use(
  response => {
    // 登录检测
    // if (response.data.errCode == 2) {
    //   router.push({
    //     path: "/login",
    //     querry: { redirect: router.currentRoute.fullPath } //从哪个页面跳转
    //   })
    // }
    return response;
  },
  error => {
    return Promise.reject(error)
  }
)

/**
 * 封装get方法
 * @param url
 * @param data
 * @returns {Promise}
 */
function get(url, params = {}, { isLoading = true, loadingMsg = '请稍后...', dataType = 'form' } = {}) {
  var loading = !isLoading || this.$loading({ background: 'rgba(0, 0, 0, 0.7)', lock: true, text: loadingMsg });
  return new Promise((resolve, reject) => {
    axios.get(url, {
        params: params
      })
      .then(response => {
        if (response.data.code !== 0) {
          console.log('api返回错误状态码::', response)
          this.$notify.error({ message: response.data.msg || 'apiCode err' });
          return;
        }
        resolve(response.data);
      })
      .catch(err => {
        console.log('api通信失败:', err)
        this.$notify.error({ message: 'api err' });
        reject(err)
      })
      .finally(function () {
        !isLoading || loading.close()
      })
  })
}

/**
 * 封装post请求
 * @param url
 * @param data
 * @returns {Promise}
 */
function post(url, params = {}, { isLoading = true, loadingMsg = '请稍后...', dataType = 'form' } = {}) {
  var loading = !isLoading || this.$loading({ background: 'rgba(0, 0, 0, 0.7)', lock: true, text: loadingMsg });
  return new Promise((resolve, reject) => {
    axios({
        method: 'post',
        url: url,
        data: params,
        dataType: dataType,
      })
      .then(response => {
        if (response.data.code !== 0) {
          console.log('api返回错误状态码::', response)
          this.$notify.error({ message: response.data.msg || 'apiCode err' });
          return
        }
        resolve(response.data);
      }, err => {
        console.log('api通信失败:', err)
        this.$notify.error({ message: 'api err' });
        reject(err)
      })
      .finally(() => {
        !isLoading || loading.close()
      })
  })
}

export { get, post }
