<template>
  <div class="home">
    <div class="task-select">
      <el-cascader v-model="selects.value" @visible-change="selectVisible($event)" :options="selects.options" :props="selects.props" @change="selectsChange"></el-cascader>
    </div>
    <div id="car_map" class="car-map">
    </div>
  </div>
</template>
<script>
/* eslint-disable */
import * as mapUtil from '@/assets/js/map.js'
import * as _ from 'lodash'
import carImg from '@/assets/img/car.png'
export default {
  name: 'home',
  data() {
    return {
      apiUrls: {
        // base: 'http://localhost:9088',
        // base: 'http://192.168.0.166:6600',
        base: '',
        lines: '/duties/tasks/device/location',
        tasks: '/duties/tasks/run/list',
        carList: '/cars/car/listAll',
      },
      selects: {
        value: [],
        options: [
          {
            label: '任务',
            value: 'task',
            children: []
          },
          {
            label: '车号',
            value: 'car',
            children: []
          },
          {
            label: '全部车辆',
            value: 'allCar'
          }
        ]
      },
      pointsParams: {
        TaskId: '',
        carId: '',
      },
      // taskId: {
      //   value: '',
      //   options: []
      // },
      // carList: {
      //   value: '',
      //   options: []
      // },
      isFirstGetLines: true,
      mp: '', // 地图实例
      map: {
        mapCenter: '', // 地图中心点 { lable: 'XXX商砼站', point: { lng: 113.67625713639688, lat: 34.89439588241682 }}
        mapZoom: 12, // 地图缩放级别
        lineColors: [ // 线路颜色
          { bg: '#DC143C', font: '#fff' }, { bg: '#FFA500', font: '#fff' }, { bg: '#006400', font: '#fff' }, { bg: '#FFFF00', font: '#000' }, { bg: '#191970', font: '#fff' }, { bg: '#4B0082', font: '#fff' },
          { bg: '#FF69B4', font: '#fff' }, { bg: '#F4A460', font: '#fff' }, { bg: '#32CD32', font: '#fff' }, { bg: '#FFD700', font: '#000' }, { bg: '#00BFFF', font: '#fff' }, { bg: '#9932CC', font: '#fff' }
         ],
        autoSetViewport: true, // 是否自动调整地图视野
        carIcon: ''
      },
      getLinesTime: 5000,
      lines: [], // 当前线路信息
      lastLines: [], // 上一次线路信息
      carIconBase64: '',
      cache: { // 变量
        createMap: true,
        test_n: 0,
        maxPoint: { lng: 0, lat: 0 },
        timer: '',
      }
    }
  },
  mounted() {
    // 初始化地图
    if (this.cache.createMap) {
      this.map.mapCenter = {
        lable: '',
        point: { "lng": 113.66562119915868, "lat": 34.77184921365784 }
      }
      this.createMap()
    }
    // 测试
    function timeout(time) {
      return new Promise((resolve, reject) => {
        setTimeout(resolve, time, `延迟了 ${time} 毫秒`)
      })
    }
    timeout(1000).then((p) => {
      return;
      // 测试车辆动画和放向
      var mp = this.mp
      var lngLats = [
        // 0 南
        [{ "lng": 113.53597774795858, "lat": 34.8489067496584 }, { "lng": 113.53597774795858, "lat": 34.789638152249864 }],
         // 1 北
        [{ "lng": 113.53597774795858, "lat": 34.8489067496584 }, { "lng": 113.53597774795858, "lat": 35.01415669575315 }],
         // 2 东
        [{ "lng": 113.53597774795858, "lat": 34.8489067496584 }, { "lng": 113.64808627560383, "lat": 34.8489067496584 }],
         // 3 西
        [{ "lng": 113.53597774795858, "lat": 34.8489067496584 }, { "lng": 113.40777158557454, "lat": 34.8489067496584 }],
         // 4 东南
        [{ "lng": 113.53597774795858, "lat": 34.8489067496584 }, { "lng": 113.6331384719178, "lat": 34.789638152249864 }],
         // 5 东北
        [{ "lng": 113.53597774795858, "lat": 34.8489067496584 }, { "lng": 113.64693644455106, "lat": 34.97299401990986 }],
         // 6 西南
        [{ "lng": 113.53597774795858, "lat": 34.8489067496584 }, { "lng": 113.43996685505215, "lat": 34.789638152249864 }],
         // 7 西北
        [{ "lng": 113.53597774795858, "lat": 34.8489067496584 }, { "lng": 113.39914785267875, "lat": 34.989556228626604 }],
         // 8 其他
        [{ "lng": 113.53597774795858, "lat": 34.8489067496584 }, { "lng": 113.50953163374484, "lat": 34.74076850085509 }],
        [{ "lng": 113.53597774795858, "lat": 34.8489067496584 }, { "lng": 113.61617846388941, "lat": 34.86881133803604 }],
      ]

      mapUtil.makCarIcon({ width: 15, height: 30 }).then((carBase64) => {

        lngLats.forEach((lngLatItem, lngLatIndex) => {
          if (lngLatIndex >= 0) {
            // if (lngLatIndex === 6) {

            var curPoint = mapUtil.newPoint(lngLatItem[0])
            var targetPoint = mapUtil.newPoint(lngLatItem[1])

            var polyline = new BMap.Polyline([mapUtil.newPoint(curPoint), mapUtil.newPoint(targetPoint)], { strokeColor: '#000', strokeWeight: 1, strokeOpacity: .9 })
            this.mp.addOverlay(polyline)

            var carIcon = new BMap.Icon(carBase64, new BMap.Size(15, 30))
            var carMarker = new BMap.Marker(curPoint, { icon: carIcon, title: String(lngLatIndex) })
            mp.addOverlay(carMarker)
            // 移动车辆测试
            mapUtil.carMove(curPoint, targetPoint, mp, carMarker, this.getLinesTime)
          }
        })

      })
    })
    // this.getTaskList()
    // this.getCarList()
    this.getMapPoints()
  },
  methods: {
    selectVisible(e) {
      if (e === true) {
        this.getTaskList()
        this.getCarList()
      }
    },
    getTaskList() {
      this.$get(this.apis.tasks).then((res) => {
        if (res.code === 0) {
          var opts = res.data.map(item => ({ label: item.name, value: item.id }))
          this.selects.options[0].children = opts
        }
      })
    },
    getCarList() {
      this.$post(this.apis.carList).then((res) => {
        if (res.code === 0) {
          var opts = res.data.map(item => ({ label: item.carNum, carNo: item.carNo, value: item.id }))
          this.selects.options[1].children = opts
        }
      })
    },
    selectsChange(val) {
      console.log(val)
      this.mp.clearOverlays()
      if (val[0] === 'task') {
        this.pointsParams = {
          TaskId: val[1],
          carId: '',
        }
        this.getMapPoints()
      } else if (val[0] === 'car') {
        this.pointsParams = {
          TaskId: '',
          carId: val[1],
        }
        this.getMapPoints()
      } else if (val[0] === 'allCar') {
        this.pointsParams = {
          TaskId: '',
          carId: '',
        }
        this.getMapPoints()
      }
    },
    getMapPoints(taskId) { // 任务
      var getPoints = () => {
        this.$post(this.apis.lines, this.pointsParams, { isLoading: this.isFirstGetLines })
          .then((response) => {
            if (response.code !== 0 || (!response.data.task && !response.data.cars)) {
              // console.log('没有数据')
              this.mp.clearOverlays()
              return
            }
            var task = response.data.task
            var cars = response.data.cars.map((item) => ({ carNo: item.carNum, lng: item.lng, lat: item.lat }))
            // console.log(response.data, cars)
            var resdata = {}
            resdata.lines = [
              {
                id: 1,
                start: {
                  label: task.name ? '起点-' + task.name : '起点',
                  point: { lng: task.startLng, lat: task.startLat }
                },
                end: {
                  label: task.receiver ? '终点-' + task.receiver : '终点',
                  point: { lng: task.lon, lat: task.lat },
                },
                cars: cars
              }
            ]
            resdata.centerPoint = {}
            // console.log(response, resdata)
            this.lastLines = JSON.parse(JSON.stringify(this.lines)) // 保存上一次车辆信息
            this.lines = resdata.lines // 当前车辆信息
            this.addOverlays()
          })
      }
      getPoints()
      this.isFirstGetLines = false
      clearInterval(this.cache.timer)
      this.cache.timer = setInterval(getPoints, this.getLinesTime)
    },
    createMap() { // 创建地图
      this.mp = new BMap.Map('car_map', { enableMapClick: false })
      // 设置中心点坐标和地图级别
      this.mp.centerAndZoom(new BMap.Point(this.map.mapCenter.point.lng, this.map.mapCenter.point.lat), this.map.mapZoom)
      this.mp.enableScrollWheelZoom()
      this.mp.enablePinchToZoom()
      this.mp.enableDoubleClickZoom()
      this.mp.addEventListener('click', function (e) { // 点击获取经纬度
        console.log(JSON.stringify(e.point))
      })
      this.cache.createMap = false
    },
    addOverlays() { // 添加,删除或更新地图覆盖物
      this.mp.clearOverlays() // 删除起始点
      var linesPointArr = []
      // 车辆和起始点
      for (let i = 0; i < this.lines.length; i++) {
        if (this.lines[i].start.point.lng && this.lines[i].start.point.lat) {
          linesPointArr.push(mapUtil.newPoint(this.lines[i].start.point))
        }
        if (this.lines[i].end.point.lng && this.lines[i].end.point.lat) {
          linesPointArr.push(mapUtil.newPoint(this.lines[i].end.point))
        }
        this.makeStartEndPoint(this.lines[i], i)
        this.makeCars(this.lines[i].cars, this.lines[i], i)
      }
      // 所有车辆坐标
      if (this.lines.length === 1) {
        this.lines[0].cars.forEach((car) => {
          linesPointArr.push(mapUtil.newPoint(car))
        })
      }
      // 调整地图视野
      let maxLng = linesPointArr.sort((a, b) => b.lng - a.lng)[0].lng
      let maxLat = linesPointArr.sort((a, b) => b.lat - a.lat)[0].lat
      if (this.map.autoSetViewport && (Math.abs(maxLng - this.cache.maxPoint.lng) > 0.005 || Math.abs(maxLat - this.cache.maxPoint.lat) > 0.005)) {
        this.cache.maxPoint.lng = maxLng
        this.cache.maxPoint.lat = maxLat
        this.mp.setViewport(linesPointArr)
      }
    },
    makeStartEndPoint(lineInfo, lineIndex) { // 起始点
      // 起点终点 红点和label
      let starAndEnd = [lineInfo.start, lineInfo.end]
      starAndEnd.forEach(item => {
        if (item && item.point.lng && item.point.lat) {
          var marker = mapUtil.overlayPointAndLable(item)
          this.mp.addOverlay(marker)
        }
      })
      // 起点终点连线
      // var polyline = new BMap.Polyline(
      //   [mapUtil.newPoint(lineInfo.start.point), mapUtil.newPoint(lineInfo.end.point)], { strokeColor: this.map.lineColors[lineIndex].bg, strokeWeight: 4, strokeOpacity: .9 }
      // )
      // this.mp.addOverlay(polyline)
    },
    makeCars(cars, line, lineIndex) { // 创建车辆
      let promise = new Promise((resolve, reject) => {
        var carIcon
        if (!this.map.carIcon) {
          mapUtil.makCarIcon({ uri: carImg, width: 16, height: 32 }).then((carBase64) => {
            this.map.carIcon = carBase64
            carIcon = new BMap.Icon(carBase64, new BMap.Size(16, 32))
            resolve(carIcon);
          })
        } else {
          carIcon = new BMap.Icon(this.map.carIcon, new BMap.Size(20, 40))
          resolve(carIcon);
        }
      })
      promise.then((carIcon) => {
        for (let i = 0; i < cars.length; i++) {
          // 是否有上一次数据
          if (this.lastLinesSimplify[line.id] && this.lastLinesSimplify[line.id][cars[i].carNo]) {
            let lastCar = this.lastLinesSimplify[line.id][cars[i].carNo]
            let curPoint = mapUtil.newPoint(cars[i])
            let lastPoint = mapUtil.newPoint(lastCar)
            let marker = new BMap.Marker(lastPoint, { icon: carIcon, title: lastCar.carNo })
            let label = new BMap.Label(lastCar.carNo, { offset: new BMap.Size(20, 0) })
            label.setStyle({ fontSize: '12px', maxWidth: '50px', textAlign: 'center', background: this.map.lineColors[lineIndex].bg, border: '0', padding: '6px', color: this.map.lineColors[lineIndex].font, borderRadius: '50%' })
            marker.setLabel(label)
            mapUtil.setRotation(lastPoint, curPoint, this.mp, marker)
            this.mp.addOverlay(marker)
            mapUtil.carMove(lastPoint, curPoint, this.mp, marker, this.getLinesTime)
          } else {
            let point = mapUtil.newPoint(cars[i])
            let marker = new BMap.Marker(point, { icon: carIcon, title: cars[i].carNo })
            let label = new BMap.Label(cars[i].carNo, { offset: new BMap.Size(20, 0) })
            label.setStyle({ fontSize: '12px', maxWidth: '50px', textAlign: 'center', background: this.map.lineColors[lineIndex].bg, border: '0', padding: '6px', color: this.map.lineColors[lineIndex].font, borderRadius: '50%' })
            marker.setLabel(label)
            mapUtil.setRotation(point, mapUtil.newPoint(line.end.point), this.mp, marker)
            this.mp.addOverlay(marker)
          }
        }
      })
    },
  },
  computed: {
    lastLinesSimplify() {
      if (this.lastLines.length === 0) {
        return this.lastLines
      } else {
        var simple = {}
        for (let i = 0; i < this.lastLines.length; i++) {
          var item = this.lastLines[i]
          simple[item.id] = _.keyBy(item.cars, 'carNo');
        }
        return simple
      }
    },
    apis() {
      var apis = {}
      var urls = this.apiUrls
      for (let key of Object.keys(urls)) {
        if (key === 'base') {
          apis.base = urls.base
        } else {
          apis[key] = urls.base + urls[key]
        }
      }
      return apis
    },
  }
}

</script>
<style>
.home {
  height: 100vh;
  user-select: none;
}
.car-map {
  height: 100vh;
}
.el-scrollbar__wrap {
  overflow: scroll;
  overflow-x: hidden;
  padding-bottom: 20px;
}
.home .task-select {
  position: fixed;
  z-index: 99;
  top: 5px;
  left: 5px;
}

</style>
