/**
 * 创建点
 */
var newPoint = point => new BMap.Point(point.lng, point.lat)
/**
 * 创建 maker
 */
var newMaker = (point) => new BMap.Marker(newPoint(point));
/**
 * 创建覆盖物-点和label
 */
var overlayPointAndLable = pointInfo => {
  var marker = newMaker(pointInfo.point)
  var label = new BMap.Label(pointInfo.label, { offset: new BMap.Size(-5, -24) })
  label.setStyle({ fontSize: "12px", maxWidth: "100px", textAlign: "center" })
  // label.disableMassClear()
  // marker.disableMassClear()
  marker.setLabel(label)
  return marker
}
/**
 * 创建车辆图标
 */
var makCarIcon = ({ uri = '/car.png', width = 20, height = 40 } = {}) => {
  var canvas = document.createElement('canvas')
  canvas.width = width
  canvas.height = height
  var ctx = canvas.getContext('2d');
  return new Promise((resolve, reject) => {
    var img = new Image()
    img.onload = () => {
      // 执行drawImage语句
      ctx.drawImage(img, 0, 0, width, height);
      var carBase64 = canvas.toDataURL()
      resolve(carBase64)
    }
    img.onerror = () => {
      reject(new Error('Could not load image from ' + uri))
    }
    img.src = uri
  })
}

/**
 * 小车从一个点移动到另一个点
 */
var effect = (curPos, targetPos, currentCount, moveCounts) => { // 每一步移动后的坐标点
  return (targetPos - curPos) * (currentCount / moveCounts) + curPos;
}
var setRotation = function (curPoint, targetPoint, mp, marker) { // 计算车辆角度值
  // console.log(curPos, targetPos)
  var curPos = mp.pointToPixel(curPoint)
  var targetPos = mp.pointToPixel(targetPoint)
  var deg = 0;
  // console.log('相对屏幕左上角::', curPos, targetPos)
  if (targetPos.x != curPos.x) {
    var tan = (targetPos.y - curPos.y) / (targetPos.x - curPos.x),
      atan = Math.atan(tan);
    deg = 360 / (2 * Math.PI) * atan
    deg = targetPos.x > curPos.x ? deg + 90 : deg - 90
    marker.setRotation(deg)
  } else {
    deg = targetPos.y - curPos.y > 0 ? 180 : 0
    marker.setRotation(deg);
  }
}
var carMove = (curPoint, targetPoint, mp, marker, moveTimes = 3000, stepTimes) => {
  // console.log(curPoint, targetPoint, mp, marker)
  stepTimes = stepTimes || moveTimes / 30
  var currentCount = 0,
    curPos = mp.getMapType().getProjection().lngLatToPoint(curPoint),
    targetPos = mp.getMapType().getProjection().lngLatToPoint(targetPoint),
    moveCounts = parseInt(moveTimes / stepTimes); // 总时长/每次移动的时长 = 需要移动的总次数
  var moveTimer = setInterval(function () {
    if (currentCount >= moveCounts) {
      clearInterval(moveTimer)
      return
    }
    if (currentCount === 0) { // 转换角度
      setRotation(curPoint, targetPoint, mp, marker)
    }
    currentCount++
    var movingX = effect(curPos.x, targetPos.x, currentCount, moveCounts),
      movingY = effect(curPos.y, targetPos.y, currentCount, moveCounts);
    //根据平面坐标转化为球面坐标
    var movingPoint = mp.getMapType().getProjection().pointToLngLat(new BMap.Pixel(movingX, movingY));
    //正在移动
    marker.setPosition(movingPoint);
  }, stepTimes);
}


export { newPoint, newMaker, overlayPointAndLable, makCarIcon, carMove, setRotation }
